<!--breadcrumb--><nav class="main-breadcrumb" aria-label="breadcrumb" v-if="settingsData" :style="{backgroundImage: `url(${settingsData.breadcrumb_bg})`}"><div class="container"> <div class="breadcrumb-title"><h2>{{ $t("orders") }}</h2></div><nav aria-label="breadcrumb"><ol class="breadcrumb"><li class="breadcrumb-item"><router-link to="/" exact>{{ $t("home") }}</router-link></li><li class="breadcrumb-item active" aria-current="page">{{ $t("orders") }}</li></ol></nav></div></nav><!--breadcrumb--><!--loading data--><LoadingData v-if="this.$store.state.loading"></LoadingData><!--loading data--><!--orders page--><section class="orders" v-if="!this.$store.state.loading"><div class="container"><div class="content"><div class="notfound-txt" v-if="ordersData.orders.length &lt; 1"><p class="oops">{{ $t("oops") }}</p><p class="notfound-text"><strong>{{ $t("no_orders") }}</strong></p></div><div class="order-details" v-for="order in ordersData.orders" :key="order.id" v-if="ordersData.orders.length &gt; 0"><div class="title"><h3>{{ $t("order_number") }}: {{ order.id }}</h3></div><table class="table"><thead><tr><th>{{ $t("product") }}</th><th>{{ $t("total") }}</th></tr></thead><tbody><tr v-for="product in order.products" :key="product.id"><td> <router-link :to="'/products/'+product.id">{{ product.title.slice(0, 20) }}... </router-link><span class="count">  {{ product.quantity }}</span></td><td>{{ product.price }}</td></tr><tr><td>{{ $t("status") }}</td><td class="status">{{ order.status }}</td></tr><tr><td>{{ $t("total") }}</td><td class="all-total">{{ order.total_price }}</td></tr></tbody></table></div></div></div></section><!--orders page-->