<nav class="main-breadcrumb" aria-label="breadcrumb" v-if="settingsData" :style="{backgroundImage: `url(${settingsData.breadcrumb_bg})`}"><div class="container"> <div class="breadcrumb-title"><h2>{{ $t("cart") }}</h2></div><nav aria-label="breadcrumb"><ol class="breadcrumb"><li class="breadcrumb-item"><router-link to="/" exact>{{ $t("home") }}</router-link></li><li class="breadcrumb-item active" aria-current="page">{{ $t("cart") }}</li></ol></nav></div></nav><!--breadcrumb--><!--cart--><section class="cart"><div class="container"><div class="cart-content"><div class="notfound-txt" v-if="cartProducts.length &lt; 1"><p class="oops">{{ $t("oops") }}!</p><p class="notfound-text"><strong>{{ $t("no_products_in_cart") }}</strong></p></div><div class="row" v-if="cartProducts.length &gt; 0"><div class="col-lg-9"><div class="table-responsive table-area"><table class="table"><thead><tr><th>{{ $t("product") }}</th><th>{{ $t("price") }}</th><th>{{ $t("quantity") }}</th><th>{{ $t("subtotal") }}</th><th> </th></tr></thead><tbody><CartItem v-for="product in cartProducts" :key="product.id" :product="product"></CartItem></tbody></table></div></div><div class="col-lg-3"><CartSummary></CartSummary></div></div></div></div></section><!--cart-->